.header {
  background: rgba(16, 16, 29, 0.9);
  position: fixed;
  width: 100%;
}

.header__row {
  display: flex;
  align-items: center;
  min-height: 100px;
}

.logo {}

.logo__text {
  font-family: Raleway, sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.2;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;

  span {
    color: #42a9ed;
    transition: color 0.3s ease;
  }

  &:hover {
    color: #42a9ed;

    span {
      color: #fff;
    }
  }
}

.menu {
  margin-left: auto;

  &__hamb {
    display: none;
  }
}

.menu__list {
  display: flex;
  min-width: 435px;
  width: 100%;
  justify-content: space-between;
}

.menu__list-item {
  list-style-type: none;
  padding: 0 10px;
}

.menu__list-item:first-child {
  padding-left: 0;
}

.menu__list-item:last-child {
  padding-right: 0;
}

.menu__link {
  font-style: normal;
  font-weight: 600;
  font-size: 13px;
  line-height: 1.2;
  text-align: right;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  transition: color 0.3s ease;

  &:hover {
    color: #43abf0;
  }
}

.menu__link.active {
  color: #43abf0;
}

@media (max-width: 998px) {
  .header__row {
    position: relative;
    min-height: 50px;
  }

  .menu__hamb,
  .hamb-icon {
    display: block;
    width: 40px;
    height: 40px;
    fill: #fff;
    transition: fill 0.3s ease;
  }

  .menu__hamb:hover .hamb-icon {
    cursor: pointer;
    fill: #42a9ed;
  }

  .menu__list {
    max-width: 320px;
    min-height: 150px;
    position: absolute;
    bottom: -150px;
    right: 0;
    flex-direction: column;
    background-color: #3D4050;
  }

  .menu__list-item {
    padding: 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .menu__link {
    display: inline-block;
    padding: 10px 10px;
  }

  .welcome__content {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .welcome__title {
    max-width: 780px;
    font-size: 38px;
    margin-bottom: 40px;
  }

  .ordering__content {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .footer__copy {
    margin-right: 20px;
    white-space: nowrap;
  }

  .pay-list {
    margin-right: 15px;
  }

  .contacts {
    white-space: nowrap;
  }
}

@media (max-width: 768px) {
  .welcome__title {
    font-size: 28px;
    margin-bottom: 25px;
  }

  .ordering__list {
    grid-template-columns: repeat(3, 1fr);
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .ordering__list-item:not(:last-child)::after {
    display: none;
  }

  .order__grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .item-ranger {
    grid-column: 2;
  }

  .footer__inner {
    flex-direction: column;
  }

  .footer__copy {
    margin-right: 0px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .pay-list {
    margin-right: 0px;
    margin-bottom: 10px;
  }

  .contacts {
    max-width: 320px;
    width: 100%;
    margin-bottom: 10px;
  }
}

@media (max-width: 576px) {
  .welcome__content {
    padding-top: 100px;
    padding-bottom: 50px;
  }

  .welcome__list {
    margin-bottom: 40px;
  }

  .ordering__title {
    font-size: 28px;
    margin-bottom: 25px;
  }

  .order__grid {
    grid-template-columns: repeat(1, 1fr);
  }

  .item-ranger {
    grid-column: 1;
  }

  input[type=text],
  input[type=email] {
    height: 44px;
  }
}

@media (max-width: 480px) {
  .ordering__list {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 400px) {
  .welcome__title {
    font-size: 24px;
  }

  .btn__row {
    flex-direction: column;
  }

  .welcome__btn {
    margin-bottom: 25px;
  }

  .item-ranger__text,
  .item-ranger__value {
    font-size: 14px;
  }

  .pay-list {
    flex-direction: column;
  }

  .pay-list {
    border-left: none;
    border-right: none;
  }

  .pay-item {
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }
}
