.ordering {
  background-image: url("../img/bg2.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.ordering__content {
  padding-top: 120px;
  padding-bottom: 150px;
}

.title {}

.ordering__title {
  font-family: Lato, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 36px;
  text-align: center;
  color: #fff;
  margin-bottom: 30px;

  span {
    color: #3e9cdc;
  }
}

.desc {}

.ordering__desc {
  font-size: 18px;
  text-align: center;
  color: #BBBBBB;
}

.list {}

.ordering__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 40px;
  margin-top: 80px;
  margin-bottom: 80px;
}

.ordering__list-item {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.ordering__list-item:not(:last-child)::after {
  position: absolute;
  top: 33%;
  right: -24%;
  transform: translate(24%, -33%);
  display: block;
  content: '';
  min-width: 110px;
  height: 6px;
  background-image: url('../img/dots.png');
}

.circle {
  position: relative;
  z-index: 2;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 95px;
  height: 95px;
  background-color: #3E9CDC;
  border: 5px solid #286690;
  border-radius: 50%;
  margin-bottom: 20px;
}

.icon {
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}


.text {
  font-family: Lato, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  color: #ffffff;

}


.order__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}

.item-ranger {
  grid-column: 1 / span 2;
}

.item-submit {
  grid-column: 2 / -2;
}

.order-select {
  display: block;
  font-family: Lato, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.2;
  display: flex;
  align-items: center;
  color: #272733;
  padding: .6em 1.4em .5em .8em;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
  border: 1px solid #aaa;
  background: rgba(255, 255, 255, 0.85);
  border-radius: 3px 3px 0px 0px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  overflow: hidden;
  background-image: url('../img/arrow.png');
  background-repeat: no-repeat;
  background-position: right .7em top 50%, 0 0;
  background-size: .65em auto, 100%;
}



.order-select::-ms-expand {
  display: none;
}

.order-select:hover {
  color: #42A9ED;
}

.order-select:focus {
  border-color: #42A9ED;
  outline: none;
}

.order-select option {
  font-weight: normal;
  border-color: red;
}




input[type=range] {
  height: 6px;
  -webkit-appearance: none;
  width: 100%;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: #c3c3c3;
  border-radius: 7px;
  border: 2px solid #fff;
}

input[type=range]::-webkit-slider-thumb {
  border: 3px solid #286690;
  height: 20px;
  width: 20px;
  border-radius: 15px;
  background: #42A9ED;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -50px;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: #c3c3c3;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  box-shadow: 0px 0px 0px #353535;
  background: #c3c3c3;
  border-radius: 7px;
  border: 2px solid #fff;
}

input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #353535;
  border: 3px solid #286690;
  height: 20px;
  width: 20px;
  border-radius: 15px;
  background: #42A9ED;
  cursor: pointer;
}

input[type=range]::-ms-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type=range]::-ms-fill-lower {
  background: #42A9ED;
  border: 2px solid #286690;
  border-radius: 15px;
}

input[type=range]::-ms-fill-upper {
  background: #42A9ED;
  border: 2px solid #286690;
  border-radius: 15px;
}

input[type=range]::-ms-thumb {
  margin-top: 1px;
  border: 3px solid #286690;
  height: 20px;
  width: 20px;
  border-radius: 15px;
  background: #42A9ED;
  cursor: pointer;
}

input[type=range]:focus::-ms-fill-lower {
  background: rgba(255, 255, 255, 0.7);
}

input[type=range]:focus::-ms-fill-upper {
  background: rgba(255, 255, 255, 0.7);
}

input[type=text],
input[type=email],
button[type=submit] {
  width: 100%;
  height: 100%;
}

input[type=text],
input[type=email] {
  padding-left: 7px;
  font-family: Lato, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.2;
  display: flex;
  align-items: center;
  color: #272733;
}

.item-submit {
  height: 48px;
}

button[type=submit] {
  weight: 48px;
  background: #3e9cdc;
  border: 1px solid #ffffff;
  border-radius: 4px;
  font-family: Lato, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  transition: all 0.3s ease;

  &:hover {
    cursor: pointer;
    background: #fff;
    border: 1px solid #3e9cdc;
    color: #3e9cdc;
  }
}

.item-file {
  width: 100%;
  position: relative;
  text-align: center;
}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__file-icon-wrapper {
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.input__file-icon {
  width: 17px;
  height: 20px;
  transition: fill 0.3s ease;
}

.input__file-button-text {
  line-height: 1;
  margin-top: 1px;
}

.input__file-button {
  width: 100%;
  min-height: 48px;
  background: #fff;
  color: #272733;
  font-family: Lato, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.2;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  border: 2px solid #3E9CDC;
  box-sizing: border-box;
  border-radius: 3px;
  padding: 8px;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background: #3E9CDC;
    color: #fff;
  }
}

.input__file-button:hover .input__file-icon {
  fill: #fff;
}

.item-ranger__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.item-ranger__text,
.item-ranger__value {
  font-family: Lato, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.2;
  color: #FFFFFF;
}
