.welcome {
  background-image: url("../img/bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.welcome__content {
  padding-top: 200px;
  padding-bottom: 200px;
}

.title {
  font-weight: bold;
  text-transform: uppercase;
}

.welcome__title {
  max-width: 780px;
  font-size: 48px;
  margin-bottom: 80px;

  span {
    color: #3e9cdc;
  }
}

.desc {}

.welcome__desc {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 10px;
}

.list {}

.welcome__list {
  margin-bottom: 80px;
}

.list__item {
  font-weight: normal;
  font-size: 20px;
  display: flex;
  align-items: center;
}

.list__item:not(:last-child) {
  margin-bottom: 5px;
}

.list__item:before {
  display: block;
  content: '-';
  color: #000;
  margin-right: 7px;
  margin-left: 7px;
}

.btn__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 396px;
  width: 100%;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 183px;
  height: 43px;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 4px;
  padding: 15px 10px;
  cursor: pointer;
  transition: all 0.3s ease;

  span {
    font-family: Lato, sans-serif;
    font-weight: 400;
    font-size: 13px;
    text-align: center;
    text-transform: uppercase;
  }

  &:hover {
    -webkit-box-shadow: 0px 0px 8px 0px rgba(62, 156, 220, 0.25);
    -moz-box-shadow: 0px 0px 8px 0px rgba(62, 156, 220, 0.25);
    box-shadow: 0px 0px 8px 0px rgba(62, 156, 220, 1);
  }
}

.welcome__btn {
  background: #3e9cdc;
}

.welcome__btn-secondery {
  color: #2A2B30;
  background: #fff;
  border-color: #6F6D7B;
}
