.footer {
  background-color: #3d4050;
  color: #fff;

  &__inner {
    min-height: 80px;
    display: flex;
    align-items: center;

    a {
      text-decoration: none;
      transition: color 0.3s ease;

      &:hover {
        color: #3E9CDC;
      }
    }
  }

  &__copy {
    font-family: Lato, sans-serif;
    font-weight: normal;
    font-size: 13px;
    line-height: 1.2;
    margin-right: 40px;

    a {
      color: #fff;
      text-transform: uppercase;
    }
  }
}

.pay-list,
.contacts {
  display: flex;
  align-items: center;

}

.pay-list {
  justify-content: space-around;
  max-width: 476px;
  width: 100%;
  margin-right: 35px;
  border-left: 1px solid #c3c3c3;
  border-right: 1px solid #c3c3c3;
}

.pay-item,
.contact-item {
  height: 20px;
  display: flex;
  align-items: center;
}

.pay-link {
  color: #c3c3c3;
}

.pay-link,
.contact-link {
  display: flex;
  align-items: center;
  height: 100%;
  font-family: Lato, sans-serif;
  font-weight: normal;
  font-size: 13px;
  line-height: 1.2;
  padding-left: 30px;
  background-position: left center;
  background-size: 20px 20px;
  background-repeat: no-repeat;
}

.pay-link--qiwi {
  background-image: url("../img/pay1.png");
}

.pay-link--ya {
  background-image: url("../img/pay2.png");
}

.pay-link--wm {
  background-image: url("../img/pay3.png");
}

.contacts {
  flex: 1;
  justify-content: space-between;
}

.contact-item {}

.contact-link {
  color: #fff;
}

.contact-link--mail {
  background-image: url("../img/mail.png");
}



.contact-link--vk {
  background-image: url("../img/vk.png");
}
